import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { BtnSearch, BtnSave } from 'components/common/button';

const { Option } = Select;

@Form.create({ name: 'trainingPlan' })
class SearchBarTrainingPlan extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" className="ant-advanced-search-form" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="H">
              {getFieldDecorator('system1', {})(
                <Select>
                  <Option value="1">i hc chnh quy</Option>
                  <Option value="2">Cao ng chnh quy</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khoa">
              {getFieldDecorator('system2', {})(
                <Select>
                  <Option value="1">C-in t</Option>
                  <Option value="2">Qun tr kinh doanh</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Kha">
              {getFieldDecorator('system3', {})(
                <Select>
                  <Option value="1">Kha 1</Option>
                  <Option value="2">Kha 2</Option>
                  <Option value="3">Kha 3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="CN">
              {getFieldDecorator('system4', {})(
                <Select>
                  <Option value="1">C kh</Option>
                  <Option value="2">in t vin thng</Option>
                  <Option value="3">in cng nghip</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Lp">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">KT1</Option>
                  <Option value="2">KT2</Option>
                  <Option value="3">KT3</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Hc k">
              {getFieldDecorator('system6', {})(
                <Select>
                  <Option value="1">Hc k 1</Option>
                  <Option value="2">Hc k 2</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={12} style={{ textAlign: 'right', marginTop: '21px' }}>
            <BtnSearch />
            <BtnSave title="Gn lp hc chung" style={{ marginRight: 0 }} />
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBarTrainingPlan;



